<template>
  <div v-title data-title="博客首页">
    <el-container>

      <el-main class="me-articles">

        <article-scroll-page></article-scroll-page>

      </el-main>

      <el-aside>

        <card-me class="me-area" :statistics="statistics"></card-me>
        <card-tag :tags="hotTags"></card-tag>

        <card-article cardHeader="最新博客" :articles="newArticles"></card-article>

        <card-article cardHeader="最热博客" :articles="hotArticles"></card-article>

<!--        <card-archive cardHeader="日期归档" :archives="archives"></card-archive>-->



      </el-aside>

    </el-container>
  </div>
</template>

<script>
  import CardMe from '@/components/card/CardMe'
  import CardArticle from '@/components/card/CardArticle'
  import CardArchive from '@/components/card/CardArchive'
  import CardTag from '@/components/card/CardTag'
  import ArticleScrollPage from '@/views/common/ArticleScrollPage'

  import {getStatistics} from '@/api/statistic'
  import {getArticles, getHotArtices, getNewArtices} from '@/api/article'
  import {getHotTags} from '@/api/tag'
  import {listArchives} from '@/api/article'

  export default {
    name: 'Index',
    created() {
      this.getStatistics()
      this.getHotArtices()
      this.getNewArtices()
      this.getHotTags()
      // this.listArchives()
    },
    data() {
      return {
        statistics: [],
        hotTags: [],
        hotArticles: [],
        newArticles: [],
        // archives: []
      }
    },
    methods: {
      getStatistics() {
        let that = this
        getStatistics().then( data => {
          that.statistics = data.data
        }).catch( error => {
          if (error !== 'error') {
            that.$message({type: 'error', message: '统计数据获取失败!', showClose: true})
          }
        })
      },

      getHotArtices() {
        let that = this
        getHotArtices().then(data => {
          that.hotArticles = data.data
        }).catch(error => {
          if (error !== 'error') {
            that.$message({type: 'error', message: '最热博客加载失败!', showClose: true})
          }
        })

      },

      getNewArtices() {
        let that = this
        getNewArtices().then(data => {
          that.newArticles = data.data
        }).catch(error => {
          if (error !== 'error') {
            that.$message({type: 'error', message: '最新博客加载失败!', showClose: true})
          }
        })

      },

      getHotTags() {
        let that = this
        getHotTags().then(data => {
          that.hotTags = data.data
        }).catch(error => {
          if (error !== 'error') {
            that.$message({type: 'error', message: '最热标签加载失败!', showClose: true})
          }
        })
      },

      // listArchives() {
      //   listArchives().then((data => {
      //     this.archives = data.data
      //   })).catch(error => {
      //     if (error !== 'error') {
      //       that.$message({type: 'error', message: '博客归档加载失败!', showClose: true})
      //     }
      //   })
      // }

    },
    components: {
      'card-me': CardMe,
      'card-article': CardArticle,
      'card-tag': CardTag,
      ArticleScrollPage,
      // CardArchive
    }
  }
</script>

<style scoped>

  .el-container {
    width: 860px;
    margin-left: -22%;
  }

  .el-aside {
    position: fixed;
    width: 260px;
    margin-left:950px
    /*right: 20%;*/
  }

  .el-main {
    padding: 0px;
    line-height: 16px;
  }

  .el-card {
    border-radius: 5px;
  }

  .el-card:not(:first-child) {
    margin-top: 20px;
  }
</style>



